<template>

  <div>
    <h3>设备类型信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="100">
          <FormItem label="设备类型名称">
            <Input v-model="formData.name" placeholder="请填写设备类型名称"></Input>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
      }, () => {
        this.load = false
      })
    },
    submit () {
      this.load = true
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'equipment_type' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

